/* =======================================*/
/* Layout Common Style
/* =======================================*/
html {
    font-size: $html-fz;
    body {
        font-family: $font-family;
        font-size: rem($body-fz);
        -webkit-font-smoothing: antialiased;
        color: $font-base;
        line-height: $line-height;
    }
}

.wrap {
    padding: 0 21px;
    min-width: $mobile;
    @include container($bp-mobile);
    @include susy-breakpoint($bp-tablet, 6) {
        @include layout(6 0/113);
        @include container($tablet);
    }
    @include susy-breakpoint($bp-desktop, 6) {
        @include layout(6 0/53);
        @include container($desktop);
    }
}

/* =======================================*/
/* ë²í¼ ê³µíµ ì¤íì¼
/* =======================================*/

/* =======================================*/
/* íì´ì§ ì¤íì¼
/* =======================================*/
.paging {
    display: inline-block;
    text-align: center;
    margin-top: 30px;
    @extend %clearfix;
    @include w-center ($position: relative, $w-percent: 50%);
    button {
        float: left;
        position: relative;
        display: inline-block;
        width: 36px;
        height: 36px;
        vertical-align: top;
        padding: 0 4px;
        border-top: 1px solid #c2c2c2;
        border-left: 1px solid #c2c2c2;
        border-bottom: 1px solid #c2c2c2;
        &:before, &:after {
            position: absolute;
            top: 12px;
        }
        &.first-prev {
            margin-left: 0;
            &:before {
                left: 9px;
                @include fa ($fa-icon: "\f053", $fz: 12px, $color: #879dad);
            }
            &:after {
                right: 11px;
                @include fa ($fa-icon: "\f053", $fz: 12px, $color: #879dad);
            }
        }
        &.prev {
            &:before {
                left: 12px;
                @include fa ($fa-icon: "\f053", $fz: 12px, $color: #879dad);
            }
        }
        &.last-next {
            border-right: 1px solid #c2c2c2;
            &:before {
                left: 10px;
                @include fa ($fa-icon: "\f054", $fz: 12px, $color: #879dad);
            }
            &:after {
                right: 9px;
                @include fa ($fa-icon: "\f054", $fz: 12px, $color: #879dad);
            }
        }
        &.next {
            &:before {
                right: 12px;
                @include fa ($fa-icon: "\f054", $fz: 12px, $color: #879dad);
            }
        }
        &:focus, &:active, &.selected {
            border-bottom: 3px solid #112a3d;
            &:before, &:after {
                color: #003761;
            }
        }
    }
    a {
        float: left;
        display: inline-block;
        width: 36px;
        height: 36px;
        line-height: 34px;
        padding: 0 4px;
        border-top: 1px solid #c2c2c2;
        border-left: 1px solid #c2c2c2;
        border-bottom: 1px solid #c2c2c2;
        &:focus, &:active, &.selected {
            font-weight: bold;
            color: #003761;
            border-bottom: 3px solid #112a3d;
        }
    }
}

/* =======================================*/
/* Skip Navigation ì¤ì 
/* =======================================*/
.skip-link {
    position: absolute;
    top: -33px;
    display: inline-block;
    width: 100%;
    height: 28px;
    line-height: 28px;
    text-align: center;
    background: #555;
    &:hover, &:focus, &:active {
        top: 0;
        font-weight: bold;
        color: #eee;
    }
}

/* =======================================*/
/* Header : Top menu
/* =======================================*/
.header {
    @extend %clearfix;
    .top-menu {
        height: 28px;
        line-height: 28px;
        border-bottom: 1px solid #ebebeb;
        .wrap {
            text-align: right;
            a {
                font-size: 12px;
                color: #757575;
                &:after {
                    display: inline-block;
                    content: "/";
                    padding: 0 6px 0 10px;
                }
                &:last-child {
                    &:after {
                        content: "";
                        padding: 0;
                    }
                }
            }
        }
    }
}

