/* =======================================*/
/* Container : ì»¨íì´ë ìì­
/* =======================================*/
#container {
    display: block;
    min-height: 670px;
    padding-top: 50px;
    padding-bottom: 30px;
    @extend %clearfix;
    /* Content ìì­ */
    .content-area {
        @include span(4 last);
        margin-bottom: 90px;
        .headline {
            height: 58px;
            line-height: 58px;
            /* Sub Page : Title ê³µíµ */
            .sub-title {
                float: left;
                position: relative;
                font-weight: bold;
                padding-left: 30px;
                &:before, &:after {
                    position: absolute;
                    top: 1px;
                }
                &:before {
                    left: 0;
                    @include fa ($fa-icon: "\f105", $fz: 30px, $color: #495157);
                }
                &:after {
                    left: 9px;
                    @include fa ($fa-icon: "\f105", $fz: 30px, $color: #495157);
                }
            }
            /* Sub Page : Location ê³µíµ */
            .site-location {
                float: right;
                a {
                    font-size: 12px;
                    color: #666;
                    img {
                        vertical-align: text-top;
                    }
                    &:before {
                        display: inline-block;
                        font-family: 'FontAwesome';
                        font-size: 16px;
                        content: "\f105";
                        padding: 0 4px 0 2px;
                    }
                    &:first-child {
                        &:before {
                            content:"";
                        }
                    }
                    &:last-child {
                        color: #333;
                    }
                }
            }
        }
        .content {
            padding: 0 10px;
            line-height: 1;
            > * {
                line-height: 1.5;
            }
        }
    }
}

