/* =======================================*/
/* ì¸í ì¤íì¼
/* =======================================*/
.input-type {
    display: inline-block;
    @include input-type;
    &.tel {
        @include input-type(71px);
        @extend %clearfix;
        .dialog {
            position: relative;
            display: inline-block;
            margin-right: 10px;
            &:after {
                display: inline-block;
                position: absolute;
                top: 4px;
                right: -10px;
                content: "-";
            }
            &:last-child {
                margin-right: 0;
                &:after {
                    content: "";
                }
            }
        }
    }
    &.e-mail {
        @include input-type(140px);
        .email {
            position: relative;
            display: inline-block;
            &:first-child {
                margin-right: 19px;
                &:after {
                    display: inline-block;
                    position: absolute;
                    top: 4px;
                    right: -18px;
                    content: "@";
                }
            }
        }
    }
    &.date {
        position: relative;
        // @include input-type(157px);
        input {
            padding-right: 0;
        }
        .icon-calender {
            position: absolute;
            top: 4px;
            right: 4px;
            width: 23px;
            height: 22px;
            vertical-align: -5px;
            background: url($icon-calender) no-repeat;
            pointer-events: none;
        }
    }
    &.full {
        @include input-type(100%);
    }
}

