/* =======================================*/
/* í­ ì¤íì¼
/* =======================================*/
.tablist {
    /* í­ì ê°¯ì t1~t8 ê¹ì§ class ìì± */
    /* @include tabs(ì»¬ë¼ ì) */
    @include tabs(8);
    width: 932px;
    margin-left: -1px;
    [role="tab"] {
        &:first-child {
            span {
                border-top-left-radius: 4px;
                border-bottom-left-radius: 4px;
            }
        }
        &:last-child {
            span {
                border-top-right-radius: 4px;
                border-bottom-right-radius: 4px;
            }
        }
        span {
            display: block;
            text-align: center;
            width: 100%;
            height: 40px;
            line-height: 36px;
            background: #f5dfd7;
            border: 2px solid #f1c7b7;
        }
        &[aria-selected='true'] {
            span {
                position: relative;
                font-weight: bold;
                color: #fff;
                line-height: 35px;
                background: #d14411;
                border: 0;
                box-shadow: 1px 1px 4px 3px rgba(#d14411, 0.3) inset;
                &:before {
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    margin-left: -5px;
                    @include arrow($dir: top, $size: 6px, $color: #fff);
                    content: "";
                }
            }
        }
        &:hover, &:focus {
            span {
                position: relative;
                font-weight: bold;
                color: #fff;
                line-height: 40px;
                background: #d14411;
                border: 0;
                box-shadow: 1px 1px 4px 3px rgba(#d14411, 0.3) inset;
                &:before {
                    position: absolute;
                    bottom: -12px;
                    left: 50%;
                    @include arrow($dir: bottom, $size: 6px, $color: #d14411);
                    content: "";
                }
            }
        }
    }
}
.panel-group {
    margin-top: 12px;
    width: 100%;
    min-height: 480px;
    [role="tabpanel"] {
        &[aria-hidden='true'] {
            display: none;
        }
    }
}